.header {
  font-family: "Source Sans Pro", Helvetica, sans-serif;
  top: 0px;
  height: 3rem;
  display: flex;
  padding-top: 20px;
}
ul {
  width: 100%;
  font-size: 18px;
  letter-spacing: 1px;
  align-items: center;
  padding-left: 10%;
  padding-right: 0%;
  font-weight: 500;
  justify-content: space-evenly;
  display: flex;
}
ul > a {
  text-decoration: none;
  color: #ffffffc2;
  justify-content: space-evenly;
  opacity: 0.7;
}
.po,
a:hover {
  color: #49bf9d;
  transition: 400ms;
  opacity: 1;
}

@media (max-width: 768px) {
  .header,
  ul {
    display: none;
  }
}
